@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-display: auto;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url(https://fonts.gstatic.com/s/roboto/v19/KFOmCnqEu92Fr1Mu4mxK.woff2) format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: auto;
  src: local("Roboto Medium"), local("Roboto-Medium"), url(https://fonts.gstatic.com/s/roboto/v19/KFOlCnqEu92Fr1MmEU9fBBc4.woff2) format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: auto;
  src: local("Roboto Bold"), local("Roboto-Bold"), url(https://fonts.gstatic.com/s/roboto/v19/KFOlCnqEu92Fr1MmWUlfBBc4.woff2) format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background: #eaf3ff;
}

::-webkit-scrollbar-thumb {
  background-color: #045dcd;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #ff9e00;
}

body {
  min-width: 320px;
  position: relative;
  font-size: 14px;
  line-height: 1.65;
  color: #a8aaac;
  font-family: "Roboto", sans-serif;
}

.content {
  max-width: calc(1139px + 2em);
  width: 100%;
  padding: 0 1em;
  margin-left: auto;
  margin-right: auto;
}

.title {
  font-size: 2.3em;
  font-weight: bold;
  color: #000000;
  line-height: 1.2;
}

.subtitle {
  font-size: 1.6em;
  font-weight: 500;
  color: #000000;
  line-height: 1.2;
}

.site-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.22em 0 0;
}

.nav--fullscreen {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #fff;
  padding: 2.22em 1em 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav--fullscreen .nav__list {
  display: flex;
  flex-direction: column;
}

.nav--fullscreen .nav__list > * {
  margin-bottom: 1.5em;
  text-align: center;
}

.nav--fullscreen .nav__list > *:last-child {
  margin: 0;
}

.nav--fullscreen .nav__list > * > * {
  width: 100%;
}

.nav--fullscreen .nav__button {
  align-self: flex-end;
  margin-bottom: 3em;
}

.nav__button {
  color: #045dcd;
  background-color: #eaf3ff;
  border: none;
  padding: 1em 1.65em;
  border-radius: 1.65em;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  line-height: 1;
  cursor: pointer;
  transition: background-color 0.5s ease, color 0.5s ease;
  display: block;
}

.nav__button:focus {
  outline: none;
}

.nav__button:active {
  outline: none;
}

.nav__button:hover {
  background-color: #045dcd;
  color: #fff;
}

.nav__list {
  list-style-type: none;
  display: none;
}

.nav__item {
  line-height: 1;
}

.nav__item button {
  border: none;
  font-family: "Roboto", sans-serif;
}

.nav__item button,
.nav__item a {
  display: block;
  color: #000000;
  font-weight: 500;
  line-height: 1;
  text-decoration: none;
  padding: 1em 1.65em;
  border-radius: 1.65em;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.5s ease, color 0.5s ease;
}

.nav__item button:hover,
.nav__item a:hover {
  background-color: #eaf3ff;
  color: #045dcd;
}

.nav__item--accent {
  margin-left: 2em;
}

.nav__item--accent button,
.nav__item--accent a {
  background-color: #eaf3ff;
  color: #045dcd;
}

.nav__item--accent button:hover,
.nav__item--accent a:hover {
  background-color: #045dcd;
  color: #fff;
}

@media (min-width: 768px) {
  .nav__button {
    display: none;
  }
  .nav__list {
    display: flex;
  }
}

.about {
  margin-top: 3em;
  margin-bottom: 6em;
  display: flex;
  flex-direction: column;
}

.about__title {
  margin-bottom: 0.7em;
}

.about__desc {
  max-width: 450px;
}

.about__image {
  width: 100%;
  height: auto;
  display: block;
  margin-bottom: 1em;
}

.about__image--tablet {
  display: none;
}

@media (min-width: 720px) {
  .about {
    margin-top: 7em;
    margin-bottom: 10em;
    flex-direction: row;
  }
  .about__text {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
  }
  .about__image--mobile {
    display: none;
  }
  .about__image--tablet {
    display: block;
  }
}

.feachers__title {
  text-align: center;
  line-height: 1;
  margin-bottom: 1em;
}

.feachers__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.feacher {
  max-width: 100%;
  width: 100%;
  margin-bottom: 2em;
  margin-top: 1em;
}

.feacher__title {
  line-height: 2;
}

.feacher__image {
  min-height: 6em;
  max-width: 100%;
  width: 100%;
}

@media (min-width: 556px) {
  .feachers__title {
    margin-bottom: 3.1em;
  }
  .feacher {
    max-width: 264px;
    padding: 0 1em;
    margin-bottom: 4em;
    margin-top: 1em;
  }
  .feacher__image {
    width: auto;
  }
  .feacher:first-child {
    padding-left: 0;
  }
  .feacher:last-child {
    margin-right: 0;
  }
}

@media (min-width: 1120px) {
  .feacher {
    margin-bottom: 7em;
  }
}
